.header {
    position: absolute;
    background-color: #ccd7e7;
    width: 100%;
    height: 40px;
    top: 0;
}
.header__logo {
    position: absolute;
    left: 0;
    width: 200px;
    height: 40px;
    background-image: url("../images/header__logo.png");
    cursor: pointer;
}
.header__container {
    position: absolute;
    width: 73%;
    right: 0;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-line-pack: center;
        align-content: center;
}
.header-tags {
    color: #00145d;
    width: 20%;
    text-align: center;
    -ms-flex-item-align: center;
        align-self: center;
    font-weight: bold;
    font-size: 1.25rem;
    cursor: pointer;
    border-left: 2px solid #00145d;
    text-decoration: none;
}


