.side-bar {
    position: absolute;
    top: 40px;
    background-color: #ecf1f5;
    height: 94%;
    width: 27%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #ccd7e7;
}
.menu {
    float: left;
    height: 300px;
    width: 100%;
    padding: 0;
    margin: 0;
    text-align: center;
}
.menu__item {
    list-style: none;
    font-size: 1.2rem;
    color: #00145d;
    font-weight: 700;
    padding: 10%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 1px solid #ccd7e7;
    cursor: pointer;
}
.menu__item:hover, .menu__item--active {
    background-color: #ccd7e7;
}
.menu__item--cursor {
    cursor: default;
}
.search {
    position: relative;
    width: 100%;
    height: 28px; 
    background-color: white;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #ccd7e7;
    border-radius: 5px;
}

.search__input {
    border: none;
    width: 80%;
    height: 25px;
    padding: 0 0 0 3%;
    position: absolute;
    left: 0;
    border-radius: 5px;
}
.search__icon {
    position: absolute;
    top: 0;
    right: 5px;
    width: 22px;
    height: 25px;
}


