@media only screen and (max-width: 480px) {
	*{
		font-size: 1rem;
	}
	#root {
    	width: 100%;
	}
	.header__logo {
		width: 35px;
		height: 40px;
		background-size: cover;
		background-image: url("./images/logo-s.png");
	}
	.header__container {
	    right: 5%;
	    width: 90%;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	}
	
	.header-tags {
	    width: 18%;
	    font-weight: bold;
	    font-size: 1rem;
	    border: none;
	}
	.news-item__close {display: none;}
}


